.profile {
  display: grid;
  grid-template: 1fr / 1fr minmax(auto, 1400px) 1fr;
}

.profile__block {
  grid-column: 2/3;
  padding: 10px;
  margin: 0;
  display: grid;
  grid-template: 1fr 1fr / 1fr minmax(auto,500px) minmax(auto,900px) 1fr;
}

.profile__block span {
  font-weight: bold;
  padding-bottom: 10px;
}

.block__data {
  grid-column: 2/3;
  grid-row: 1/2;

  background-color: rgb(245, 245, 245);
  text-align: center;
  padding: 10px;
}

.data__title{
  display: grid;
  text-align: center;
  font-size: 25px;
  font-weight: 800;
}

.block__form {
  grid-column: 3/4;
  grid-row: 1/2;

  padding-left: 10px;
  background-color: rgb(245, 245, 245);
  margin-left: 20px;
  text-align: left;
  padding: 10px;
}

.form__block {
  padding-left: 10px;
  color: rgb(77, 77, 77);
}

.block__wall {
  grid-column: 3/4;
  grid-row: 2/3;

  padding-left: 10px;
  background-color: rgb(245, 245, 245);
  margin-left: 20px;
  margin-top: 20px;
  padding: 10px;
}

.general__subscribe,
.general__button {
  color: white;
  background-color: black;
  margin: 10px 3px;
}
.general__subscribe:hover,
.general__button:hover {
  transform: scale(1.1);
  color: rgb(147, 109, 76);
}